import React, { Component } from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';
import styled from 'styled-components';
import CreateUserForm from './CreateUserForm';
import {
  validateFirstName,
  validateUsername,
  validatePermissions,
  validatePassword,
  validateConfirmPassword,
  comparePasswords,
} from '../lib/validation';

const CreateUserStyles = styled.div`
  h4 {
    color: ${props => props.theme.red};
  }

  h5 {
    margin-bottom: 1rem;
  }

  .form-row.permissions .row-full {
    @media screen and (min-width: 768px) {
      display: flex;
      flex-direction: row;

      label p {
        margin-right: 1rem;
        margin-left: 0;
      }
    }
  }
`;

const CREATE_USER_MUTATION = gql`
  mutation CREATE_USER_MUTATION(
    $firstName: String!
    $lastName: String
    $company: String
    $email: String
    $address: String
    $phone: String
    $username: String!
    $password: String!
    $permissions: String!
    $canOrder: Boolean!
  ) {
    createUser(
      data: {
        firstName: $firstName
        lastName: $lastName
        company: $company
        email: $email
        address: $address
        phone: $phone
        username: $username
        password: $password
        permissions: $permissions
        canOrder: $canOrder
        active: true
      }
    ) {
      id
    }
  }
`;

class CreateUser extends Component {
  state = {
    firstName: '', // required
    lastName: '',
    username: '', // required
    company: '',
    email: '',
    permissions: '', // required
    canOrder: false, // required - needs to be false by default
    phone: '',
    address: '',
    password: '', // required
    confirmPassword: '', // required
    error: {},
  };

  handleChange = e => {
    const { name, type, value } = e.target;
    const val = type === 'number' ? parseFloat(value) : value;
    this.setState({ [name]: val });
  };

  handleCheckbox = e => {
    const { name } = e.target;
    this.setState({ [name]: !this.state.canOrder });
  };

  validateInput = e => {
    const { name, value } = e.target;
    const error = {};

    if (name === 'firstName' && validateFirstName(value)) {
      error.firstName = validateFirstName(value);
    }

    if (name === 'username' && validateUsername(value)) {
      error.username = validateUsername(value);
    }

    if (name === 'permissions' && validatePermissions(value)) {
      error.permissions = validatePermissions(value);
    }

    this.setState({
      error: {
        ...this.state.error,
        ...error,
      },
    });
  };

  validateForm = () => {
    let isValid;
    const error = {};
    const {
      firstName,
      username,
      permissions,
      password,
      confirmPassword,
    } = this.state;

    if (validateFirstName(firstName)) {
      error.firstName = validateFirstName(firstName);
    }

    if (validateUsername(username)) {
      error.username = validateUsername(username);
    }

    if (validatePermissions(permissions)) {
      error.permissions = validatePermissions(permissions);
    }

    this.setState({ error });
  };

  resetState = e => {
    this.setState({
      firstName: '', // required
      lastName: '',
      username: '', // required
      company: '',
      email: '',
      permissions: '', // required
      canOrder: false, // required - needs to be false by default
      phone: '',
      address: '',
      password: '', // required
      confirmPassword: '', // required
      error: {},
    });

    window.scrollTo(0, 0);
  };

  render() {
    const errorMessage = this.state.error;

    return (
      <Mutation mutation={CREATE_USER_MUTATION} variables={this.state}>
        {(createUser, { loading, error }) => (
          <CreateUserForm
            handleChange={this.handleChange}
            handleCheckbox={this.handleCheckbox}
            validateInput={this.validateInput}
            validateForm={validateForm}
            resetState={this.resetState}
            error={errorMessage}
          />
        )}
      </Mutation>
    );
  }
}

export default CreateUser;
